import '../state/webhooks';
import '../styles/webhooks';

import React, {Component} from 'react'
import Loader from '../components/Loader'

import {getStore} from 'tbg-flux-factory';
const webhooksStore = getStore('webhooks');

import SelectSearch from 'react-select-search'

class Webhooks extends Component {
  constructor(props) {
    super(props);

    this.state = Object.assign({}, webhooksStore.getState(), {
      selectedApp: null
    })
    this.handleStoreUpdate = this.handleStoreUpdate.bind(this);
  }

  componentWillMount() {
    webhooksStore.addChangeListener(this.handleStoreUpdate);
  }

  componentWillUnmount() {
    webhooksStore.removeChangeListener(this.handleStoreUpdate);
  }

  componentDidMount() {
    webhooksStore.Actions.getWebhooks()
  }

  handleStoreUpdate() {
    this.setState(webhooksStore.getState(), () => {

    })
  }

  onSelectChange(e) {
    console.log(e)
  }

  renderTWEventsList() {
    if (this.state.webhooks.length < 1) return null

    const options = this.state.webhooks.map(({id, name}) => {
      return { value: id, name: name}
    })

    return (
      <SelectSearch
        options={options}
        placeholder="Select TW webhook" />
    )
  }

  renderAppSelections() {
    if (!this.state.selectedApp) {
      return (
        <div className='app-selection'>
          <button className='app-selection__btn' onClick={() => { this.setState({selectedApp: 'teamwork'})}}>TW</button>
          <button className='app-selection__btn' onClick={() => { this.setState({selectedApp: 'github'})}}><i className="fa fa-github" /></button>
        </div>
      )
    }
    if (this.state.selectedApp === 'teamwork') {
      return (
        <div className='app-selection'>
          <i className="fa fa-close hooks__close" onClick={() => { this.setState({ selectedApp: null})}} />
          <h3 className="app-selection__title">Teamwork.com</h3>
          {this.renderTWEventsList()}
        </div>
      )
    }
    if (this.state.selectedApp === 'github') {
      return (
        <div className='app-selection'>
          <i className="fa fa-close hooks__close" onClick={() => { this.setState({ selectedApp: null})}} />
          no github integrations
        </div>
      )
    }
  }

  render() {
    return (
      <div>
        <p className='on fancy'><span>On</span></p>
        {this.renderAppSelections()}
      </div>
    );
  }
}

export default Webhooks



