import {createStore, getStore} from 'tbg-flux-factory';
import request from 'reqwest';

var authStore = getStore('auth');

function fetchWebhooks () {
  const { baseURL, auth_token } = authStore.getState()

  return request({
    url: `${baseURL}/webhooks/events.json`,
    type: 'json',
    method: 'get',
    contentType: 'application/json',
    headers: {
      "Authorization": "BASIC " +  auth_token,
    },
    crossOrigin: true,
  })
}

const webhooks = createStore({
  name: 'webhooks',
  data: {
    loading: false,
    webhooks: []
  },
  actions: {
    server: {
      getWebhooks() {
        this.setState({ loading: true })

        fetchWebhooks()
        .then((resp) => {
          this.setState({ webhooks: resp.events })
        })
        .always( () => {
          this.setState({ loading: false })
        })
      }
    },
    view: {}
  }
});

export default webhooks;



